.form-container {
    /* position: relative; */
    /* margin-top: 60px; */
    
    display: flex;
    align-items: center;
    justify-content: center;
    /* min-height: 70vh; */
    flex-direction: column;
    background-color: rgb(196, 222, 233);
    /* background-image: "backgrounf.jpg"; */
    /* background-color: #ffdee9; */
    /* background-image: linear-gradient(0deg, #ffdee9 0%, #b5fffc 100%); */
   
  }
  
  .form-container form {
    /* position: absolute; */
    /* top: 25%;
    left: 5%; */
    box-shadow:rgb(108,172,197) 0px 5px 15px;
    padding: 20px;
    background-color:rgb(108,172,197);
    margin: 1rem;
    /* border-radius: 10px; */
    /* height: 460px;
    width: 500px; */
  }
  
  .form-container form .title {
    text-align: center;
    /* margin-bottom: 15px; */
    font-weight: bold;
    font-family: "Playfair Display", serif;
  }
  
  .form-container form input {
    border: none;
    /* border-bottom: 1px solid #000; */
    border-radius: 10;
    background-color: rgb(196, 222, 233);
  }
  .form-container form input:onclick { 
    background-color:rgb(177, 201, 211);

  }
  .form-container form button {
    border: 1px solid black;
    border-radius: 10;
    background-color: #000;
    color: white;
    /* width: 460px; */
  }
  
  .form-container form button:hover {
    border: 1px solid black;
    background: #000000; /* fallback for old browsers */
    background: -webkit-linear-gradient(
      to right,
      #434343,
      #000000
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
      to right,
      #434343,
      #000000
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  }
  
  .forgot-btn {
    background-color: #434343a4 !important;
    border: 0 !important;
    width: -webkit-fill-available;
    
  }
  
  .forgot-btn:hover {
    color: white !important;
  }
  